<template lang="pug">
  .aboutYFP
    .container.px-0
      .pageTitle.text-center.mt-30.mb-5.font-weight-bold.px-2 
      .text-center
        img.img-fluid.aboutYFPImg(src="../../../static/aboutYFP.jpg")
    .container
      .pageTitle.text-center.mt-30.mb-5.font-weight-bold.px-2 
      .row.justify-content-center
        .col-12.px-0
          p.aboutYFPImgDes.mt-4.text-justify.px-2 
          p.aboutYFPImgDes.aboutYFPImgDesSecond.text-justify.px-2 
      .pageTitle.text-center.mt-4.font-weight-bold.mb-5 
    .container-fluid.printExperienceBox
      .row.align-items-center.flex-column.mx-0
        .border.border-dark.px-3.py-2.pageTitle.mb-5.mt-4 
        p.printExperienceFirstDes.aboutYFPImgDes.text-justify.mb-5.px-2 
        .row.printExperienceFirstDes.mx-0.justify-content-center
          .col-md-3.col-6.mb-4.mx-md-3.px-1
            img.img-fluid(src="../../../static/001.png")
          .col-md-3.col-6.mb-4.mx-md-3.px-1
            img.img-fluid(src="../../../static/002.png")
          .col-md-3.col-6.mb-4.mx-md-3.px-1
            img.img-fluid(src="../../../static/003.png")
          .col-md-3.col-6.mb-4.mx-md-3.px-1
            img.img-fluid(src="../../../static/004.png")
    .container-fluid.px-0
      .row.mx-0
        .col-md-6.px-0.col-12
          img.img-fluid(src="../../../static/wontregret.jpg")
        .col-md-6.wontRegretBlueBg.col-12
          .d-flex.flex-column.w-100.h-100.justify-content-center.align-items-center
            p.pageTitle.text-white.wontRegretTitle 
            p.aboutYFPImgDes.text-white.mb-4.wontRegretSecondTitle Your best choice.
            button.btn.btnInPage.py-0.pr-0.text-primary <span class="font-weight-bold"></span> <i class="fas fa-chevron-right fa-xs"></i>
    .container-fluid.px-0
      .row.mx-0.reverse
        .col-md-6.col-12.wontRegretDarkBg
          .d-flex.flex-column.w-100.h-100.justify-content-center.align-items-center
            p.pageTitle.text-white.wontRegretTitle 
            p.aboutYFPImgDes.text-white.mb-4.wontRegretSecondTitle We are always waiting for you.
            button.btn.btnInPage.py-0.pr-0.text-primary <span class="font-weight-bold"></span> <i class="fas fa-chevron-right fa-xs"></i>
        .col-md-6.col-12.px-0
          img.img-fluid(src="../../../static/chair.jpg")
    .container.px-0.mt-8.mb-5
      .row.justify-content-center.w-100.mx-auto.px-0
        h2.aboutYFPImgDes.text-center.font-weight-bold.w-100 
        p.text-center.w-100.eachSecondTitle.px-2 
        .bgSetting
          img.my-30.newEditor(src="../../assets/img/home/3c.jpg")
</template>

<script>
export default {
  created () {
    this.$store.commit('sideBarShowEvent', false)
  }
}
</script>

<style lang="scss" scoped>
$nototc: "Noto Serif TC",serif;
.pageTitle{
  font-size: 32px;
  font-family:$nototc;
  font-weight: bold;
}
.aboutYFPImg{
  max-height: 760px;
  min-width: 100%;
}
.aboutYFPImgDes{
  font-size: 24px;
  font-family: $nototc;
  font-weight: bold;
}
.aboutYFPImgDesSecond{
  margin-bottom: 50px;
}
.printExperienceBox{
  background: #c1bfc1;
  padding: 20px 0;
  box-sizing: border-box;
}
.printExperienceFirstDes{
  max-width: 1300px;
  .col-md-3{
    max-width:226px;
    padding: 0;
  }
  img{
    max-width: 226px;
  }
}
@media(max-width: 640px){
  .aboutYFPImgDes{
    padding: 0 16px;
  }
  .printExperienceFirstDes{
    max-width: 1300px;
    padding: 0 12px;
    img{
      max-width: 100%;
    }
  }
}
.wontRegretBlueBg{
  background: #577f99;
  box-sizing: border-box;
}
.wontRegretDarkBg{
  background: #44505e;
  // padding: 50px 0;
  box-sizing: border-box;
}
@media(max-width: 640px){
  .wontRegretBlueBg{
    padding: 50px 0;
  }
  .wontRegretDarkBg{
    padding: 50px 0;
  }
  .wontRegretTitle{
    font-size: 24px;
  }
  .wontRegretSecondTitle{
    font-size: 18px;
  }
}
// 
.btnInPage{
  border-radius: 5px;
  width: 203px;
  height: 39px;
  font-size: 21px;
  padding-left: 50px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  .fa-chevron-right{
    margin-left: 37px;
  }
}
// reverse 
@media(max-width: 640px){
  .reverse{
    flex-direction: column-reverse;
  }
}
// new editor 
.bgSetting{
  max-width:520px;
  height:auto;
  box-sizing: border-box;
  display: flex;
  align-items:center;
  justify-content: center;
  img.newEditor{
    width: 90%;
    height: auto;
  }
}
@media(max-width:640px){
  img.newEditor{
    max-width: 90%;
    height: auto;
  }
}
</style>



